}
.page-btn {
  @include page-btn();
}

.swiper-pagination-bullet {
  background: #fff;
  opacity: 1;
  margin: 0 12px !important;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #0a1e3c;
}
.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 35px;
}

.title {
  .title__wrapper {
  }
  .title__slider {
  }
  .title__slide {
    height: 465px;
    position: relative;
    padding: 98px 113px 98px 120px;
    overflow: hidden;

    border-radius: 0 50px 0 50px;
    clip-path: polygon(4% 0,100% 0,100% 90%,96% 100%,0 100%,0 8%);
  }
  .title__slide-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: left;
  }
  .title__slide-body {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
  }
  .title__slide-img {
    &.--desc {

    }
    &.--mob {
      display: none;
    }
  }
}


.top-new {
  padding: 30px 0 60px;
  .top-new__wrapper {
  }
  .top-new__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 69px 114px 40px;

    border: 1px solid #000;
    border-radius: 0 50px 0 50px;
  }
  .top-new__left {
    color: #000;
  }
  .top-new__date {
    font-size: 15px;
  }
  .top-new__header {
    color: #0a1e3c;
    margin: 12px 0;
  }
  .top-new__text {
  }
  .top-new__right {
    max-width: 420px;
    width: 100%;
    object-fit: cover;
    border-radius: 20px;
    margin-left: 38px;
    flex-shrink: 0;
    overflow: hidden;
  }
}

.competencies {
  padding-bottom: 60px;
  &.--page {
    .competencies__header {
      text-align: left;
    }
  }
  .competencies__wrapper {
  }
  .competencies__header {
  }
  .competencies__text {
    margin-top: 60px;
  }
  .competencies__items {
    margin-top: 40px;
    position: relative;
    &:not(.swiper-initialized) .swiper-wrapper {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 40px;
    }
  }
  .competencies__items-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .competencies__item {
    height: 190px;
    display: flex;
    justify-content: end;
    flex-direction: column;
    color: #fff;
    position: relative;
    padding: 32px 33px;
    border-radius: 20px;
    overflow: hidden;

    font-size: 28px;
    line-height: 1.2;
    font-weight: 500;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      opacity: .9;
    }
  } 
  
}

.news {
  padding-bottom: 60px;

  &.--page {
    .news__header {
      text-align: left;
    }
  }
  .news__wrapper {
    display: flex;
    flex-direction: column;
  }
  .news__header {
  }
  .news__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-top: 40px;
  }
  .news__item {
  }
  .news__img {
    height: 202px;
    border-radius: 20px;
    overflow: hidden;
    margin-bottom: 12px;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .news__body {
  }
  .news__date {
    font-size: 15px;
    color: #0a1e3c;
    font-family: 'Inter';
    font-weight: 500;
    line-height: 1.2;
    margin-bottom: 12px;
  }
  .news__name {
    margin-bottom: 12px;
    color: #0a1e3c;
  }
  .news__text {
    color: #000;
  }
  .news__all {
    margin: 38px auto 0;
  }
}

.subscribe {
  padding-bottom: 60px;
  .subscribe__wrapper {
    color: #fff;
    padding: 47px 112px 64px;
    background: #69c0ff;
    border-radius: 0 75px 0 75px;
    clip-path: polygon(5% 0,100% 0%,100% 80%,95% 100%,0 100%,0 20%);

  }
  .subscribe__body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 32px;
  }
  .subscribe__header {
    font-family: 'Euclid Square';
    font-weight: 400;
    font-size: 48px;
    line-height: 42px;
    letter-spacing: 0%;
    margin: 0;
  }
  .subscribe__form {
  }
  .subscribe__field {
    border: none;
    outline: none;
    font-size: 32px;
    padding: 0;
    border-bottom: 1px solid #fff;
    padding-bottom: 14px;
    font-size: 32px;
    margin-bottom: 25px;
    color: #fff;
    background: transparent;
    display: block;
    width: 100%;
  }
  .subscribe__btn {
    max-width: 100%;
    width: 100%;
  }
}

@media screen and (max-width: 1350px) {
  .xs-hidden {
    display: none;
  }
  
  .title .title__slide-arrow {
    display: none;
  }
  .title .title__slide-img{
    margin: 0 auto;
    &.--desc {
      display: none;
    }
    &.--mob {
      display: block;
    }
  }
  .title .title__slide {
    height: auto;
    padding: 50px 48px 142px;
    clip-path: polygon(4% 0,100% 0,100% 90%,90% 100%,0 100%,0 8%);
  }

  .top-new .top-new__body {
    padding: 0;
    flex-direction: column-reverse;
    border-radius: 20px;
  }
  .top-new .top-new__right {
    max-width: 100%;
    margin: 0;
    width: 100%;
  }
  .top-new .top-new__left {
    padding: 10px 20px 0;
  }
  .top-new .top-new__header {
    margin: 10px 0;
  }
  .competencies .competencies__item {
    height: 290px;
    justify-content: center;
    font-size: 24px;
    font-weight: 500;
    padding: 30px;
  }

  .news .news__items {
    grid-template-columns: 1fr;
  }

  .subscribe .subscribe__wrapper {
    padding: 60px 49px;
    clip-path: polygon(10% 0,100% 0,100% 90%,90% 100%,0 100%,0 10%);
  }
  .subscribe .subscribe__body {
    display: flex;
    justify-content: start;
    flex-direction: column;
    align-items: center;
  }
  .subscribe .subscribe__header {
    font-size: 32px;
    line-height: 1.1;
  }
}